import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SalesOrderViewComponent } from './sales-order-view.component';
import { RouterModule, Routes } from '@angular/router';
import { SalesOrderService } from 'src/app/service/sales-order.service';
import { SalesOrderDetailsComponent } from './sales-order-details/sales-order-details.component';
import { SalesOrderPicklistComponent } from './sales-order-picklist/sales-order-picklist.component';
import { PaginationModule } from 'src/app/common-module/pagination/pagination.module';
import { TableListModule } from 'src/app/common-module/table-list/table-list.module';
import { PickListService } from 'src/app/service/pick-list.service';

const routes: Routes = [
  {
    path: '',
    component: SalesOrderViewComponent,
    children: [
      {
        path: '',
        component: SalesOrderDetailsComponent,
        data: { title: 'view_sales_order' },
      },
      {
        path: 'picklist',
        component: SalesOrderPicklistComponent,
        data: { title: 'view_sales_order' },
      }
    ]
  },
];

@NgModule({
  declarations: [SalesOrderViewComponent, SalesOrderDetailsComponent, SalesOrderPicklistComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    PaginationModule,
    TableListModule
  ],
  providers: [SalesOrderService, PickListService]
})
export class SalesOrderViewModule { }
