import { Component, OnInit } from '@angular/core';
import { DataService } from 'src/app/service/data.service';
import { PaginationService } from 'src/app/service/pagination.service';
import { ISubscription } from "rxjs/Subscription";
import { Location } from '@angular/common';
import { ActivatedRoute } from '@angular/router';
import { ActionField, TextField, StatusField } from 'src/app/common-module/table-list/list-field-type';
import { PickListService } from 'src/app/service/pick-list.service';
import { errorMessage } from 'src/app/messages';
declare var swal: any;
@Component({
  selector: 'app-sales-order-picklist',
  templateUrl: './sales-order-picklist.component.html'
})
export class SalesOrderPicklistComponent implements OnInit {

  objectArray: any;
  viewId: any;
  orderProductListArray: Array<any> = [];
  headerData: Array<any> = [];
  loadingState = false;
  pagination: any;
  isGenerate: boolean = false;
  currentPage: number = 1;
  private subscription: ISubscription;

  constructor(
    private dataService: DataService,
    private paginationService: PaginationService,
    private picklistService: PickListService,
    private location: Location,
    private activatedRoute: ActivatedRoute
  ) {

    this.activatedRoute.parent.params.subscribe((response) => {
      this.viewId = response['id'];
    });
    this.subscription = this.dataService.SalesOrderDetails.subscribe((data) => {
      if (data) {
        this.objectArray = data;
      }
    });
  }

  ngOnInit() {
    this.setHeaderData();
    this.getOrderProductList();
  }

  back() {
    this.location.back();
  }

  setHeaderData() {
    this.headerData.push(

      new TextField({
        label: "UN NO",
        key: 'pick_list_index',
      }),
      new TextField({
        label: "SKU no",
        key: 'product.sku_no',
      }),
      new TextField({
        label: "Product",
        key: 'product.product_name',
      }),
      new TextField({
        label: "Product Qty",
        key: 'product_qty',
      }),
      new StatusField({
        label: "Status",
        key: 'status.label',
      })
    );

    let canView = true;
    let actions = {};
    if (canView) {
      actions['edit'] = {
        path: '/outbound/pick-list/view/',
        id: 'pick_list_id',
        class: 'fa-eye',
        showText: 'View'
      }
      actions['view'] = {
        path: '/outbound/pick-list/generate-pick-list/',
        id: 'pick_list_id',
        class: 'fa-file-pdf-o',
        showText: 'Submit Report'
      }
    }
    let actionColom = new ActionField(actions);
    this.headerData.push(
      actionColom
    );
  }

  onReloadEvent() {
    this.getOrderProductList();
  }

  getOrderProductList() {
    this.picklistService.getPickListGeneratedListById(this.viewId).subscribe((response) => {
      if (response.success && response.data) {
        this.orderProductListArray = response.data.list ? response.data.list : [];
        this.isGenerate = response.data.is_generate;
        // this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.orderProductListArray = [];
        this.loadingState = false;
        this.isGenerate = false;
        this.pagination = null;
      }
    }, (error) => {
      this.orderProductListArray = [];
      this.isGenerate = false;
      this.loadingState = false;
      this.pagination = null;
    });
  }

  deleteObject(object) {
    swal.fire({
      title: errorMessage.delete_header_text,
      text: errorMessage.delete_smalll_text,
      icon: errorMessage.delete_dialogue_type,
      showCancelButton: true,
      confirmButtonText: errorMessage.delete_confirm_button,
      cancelButtonText: errorMessage.delete_cancel_button,
    }).then((result) => {
      if (result.value) {
        const params: any = { pick_list_detail_id: object.pick_list_detail_id };
        this.picklistService.deletePickListSubmitReports(object.pick_list_id, params).subscribe(
          (response) => {
            if (response.success) {
              this.getOrderProductList();
            }
          },
        );
      }
    }).catch(swal.noop);
  }


  ngOnDestroy() {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }
}
