import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { PaginationService } from 'src/app/service/pagination.service';
import { DataService } from 'src/app/service/data.service';
import { ISubscription } from "rxjs/Subscription";
import { TextField } from 'src/app/common-module/table-list/list-field-type';
import { SalesOrderService } from 'src/app/service/sales-order.service';

@Component({
  selector: 'app-sales-order-details',
  templateUrl: './sales-order-details.component.html'
})
export class SalesOrderDetailsComponent implements OnInit {

  viewId: string;
  objectArray: any;
  pagination: any;
  orderProductListArray: Array<any> = [];
  headerData: Array<any> = [];
  loadingState = false;
  currentPage: number = 1;
  private subscription: ISubscription;
  currentCompany:any;
  date_format:string="M/d/yy";
  timezone:string
  time_format:string;
  constructor(private activatedRoute: ActivatedRoute,
    private salesorderService: SalesOrderService,
    private dataService: DataService,
    private paginationService: PaginationService
  ) {
    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.viewId = this.activatedRoute.snapshot.paramMap.get('id');
    }
  }

  ngOnInit() {
    this.dataService.currentCompany.subscribe((response)=>{
      if(response){
      this.timezone=String(response.timezone.format);;
      this.time_format=response.time_format.label;
      this.date_format=response.date_format.label;
      this.currentCompany=response;
      }
      
    });
    this.getOrderDetails();
    this.setHeaderData();
    this.getOrderProductList();
  }

  setHeaderData() {
    this.headerData.push(
      new TextField({
        label: "SKU No #",
        key: 'sku_no',
      }),
      new TextField({
        label: "Product",
        key: "product_name",
      }), new TextField({
        label: "Order Qty",
        key: "qty",
      }),
    );

  }


  getOrderDetails() {
    this.subscription = this.dataService.SalesOrderDetails.subscribe((data) => {
      if (data) {
        this.objectArray = data;
      }
    });
  }

  getPage(page: number) {
    this.currentPage = page;
    this.currentPage = 1;
    this.getOrderProductList();
  }

  getOrderProductList() {
    const params: any = { sales_order_id: this.viewId };
    this.salesorderService.getSalesOrderProductList(params).subscribe((response) => {
      if (response.success && response.data) {
        this.orderProductListArray = response.data.list;
        this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.orderProductListArray = [];
        this.loadingState = false;
        this.pagination = null;
      }
    }, (error) => {
      this.orderProductListArray = [];
      this.loadingState = false;
      this.pagination = null;

    });
  }


  ngOnDestroy() {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }

}
